.main-container {
  width: 100%;
  height: 100%;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.typewriter-text {
  overflow: hidden;
  position: relative;
  width: 18em;
  border-right: 2px solid rgba(255, 255, 255, 0.75);
  font-size: 38px;
  white-space: nowrap;
  margin-top: 100px;

  animation: typewriter 4s steps(35) 1s 1 normal both,
    blinkTextCursor 800ms steps(30) infinite normal;
}

/*

  animation-name: typewriter;
  animation-duration: 4s;
  animation-timing-function: steps(44);
  animation-delay: 1s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;

*/

.intro-summary {
  font-size: 25px;
  margin-top: 10px;
  text-align: center;
  margin-bottom: 25px;
}

@keyframes typewriter {
  from {
    width: 0;
  }

  to {
    width: 9em;
  }
}

@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }

  to {
    border-right-color: transparent;
  }
}
